.form_search {
  display: flex;
  flex-direction: column;
  margin-top: 30px;

  @media screen and (min-width: 834px) {
    padding-top: 50px;
  }
  @media screen and (min-width: 1024px) {
    width: 45%;
  }
}
.container_search {
  @media screen and (min-width: 1024px) {
    display: flex;
    position: relative;

    width: 100%;
    align-items: center;
    justify-content: space-between;
    gap: 60px;
  }
}

.input_label {
  display: flex;
  flex-direction: column;
  position: relative;
  font-weight: 300;
  font-size: 12px;
  line-height: 19.5px;
  @media screen and (min-width: 834px) {
    font-size: 15px;
  }
}
.loupe_input {
  position: absolute;
  top: 45px;
  left: 5px;
}
.btn_add {
  width: 66px;
  height: 32px;
  background-color: #ffffff;
  cursor: pointer;

  font-weight: 300;
  font-size: 12px;
  line-height: 10px;
  @media screen and (min-width: 834px) {
    font-size: 15px;
    line-height: 19.5px;
  }
  @media screen and (min-width: 1024px) {
    margin-right: 60px;
  }
}
.container_change {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}
.input_search {
  margin-top: 20px;
  width: 216px;
  height: 32px;
  padding: 0 12px 0 30px;
  outline: none;
  border: 4px solid black;

  font-weight: 400;
  font-size: 12px;
  line-height: 14.5px;
  @media screen and (min-width: 667px) {
    width: 304px;
  }
  @media screen and (min-width: 834px) {
    width: 456px;
    font-size: 16px;
    line-height: 19.5px;
  }

  @media screen and (min-width: 1024px) {
    width: 283px;
  }
}
.input_search_url {
  padding-left: 12px;
}
.label_form {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 27px;
  position: relative;

  font-weight: 300;
  font-size: 12px;
  line-height: 10px;

  @media screen and (min-width: 834px) {
    font-size: 15px;
    line-height: 19.5px;
  }
}
.btn_check {
  position: absolute;
  width: 50px;
  height: 25px;
  border: 3px solid #79747e;
  background: #e6e0e9;
  border-radius: 20px;
  box-shadow: inset 1px 1px 2px #0000001a;
  cursor: pointer;
  right: 2%;
  @media screen and (min-width: 667px) {
    right: 5%;
  }
  @media screen and (min-width: 834px) {
    right: 0%;
  }
  @media screen and (min-width: 1024px) {
    right: 10%;
  }
}
.switching {
  display: block;
  top: 8%;
  left: 8%;
  width: 16px;
  position: absolute;
  height: 16px;
  transition: left 0.35s;
  border-radius: 50%;
  background-color: #79747e;
}
.switching_active {
  display: block;
  top: 8%;
  left: 53%;
  width: 16px;
  position: absolute;
  height: 16px;
  border-radius: 50%;
  transition: left 0.35s;
  background-color: #ffffff;
}
.btn_check_active {
  border: 3px solid #6750a4;
  background: #6750a4;
  position: absolute;
  transition: background 0.35s;
  width: 50px;
  height: 25px;
  border-radius: 20px;
  box-shadow: inset 1px 1px 2px #0000001a;
  cursor: pointer;
  right: 2%;
  @media screen and (min-width: 667px) {
    right: 5%;
  }
  @media screen and (min-width: 834px) {
    right: 0%;
  }
  @media screen and (min-width: 1024px) {
    right: 10%;
  }
}
.btn_information,
.btn_submit {
  height: 26px;
  padding: 0;
  background-color: #ffffff;
  @media screen and (min-width: 667px) {
    margin-right: 30px;
  }
  @media screen and (min-width: 834px) {
    height: 36px;
    margin-right: 0;
  }
}
.text_op {
  margin-top: 30px;
  font-weight: 300;
  font-size: 12px;
  line-height: 10px;
  @media screen and (min-width: 834px) {
    font-size: 15px;
    line-height: 19.5px;
  }
}
.btn_information {
  width: 75px;
  font-weight: 300;
  font-size: 10px;
  line-height: 10px;
  margin-top: 30px;
  cursor: pointer;
  @media screen and (min-width: 834px) {
    width: 190px;
    font-weight: 300;
    font-size: 16px;
    line-height: 19.5px;
  }
}
.btn_submit {
  width: 84px;
  font-weight: 300;
  font-size: 12px;
  line-height: 10px;
  cursor: pointer;
  @media screen and (min-width: 834px) {
    width: 100px;
    font-size: 16px;
    line-height: 19.5px;
  }
}
.line {
  border: 3px solid #ffd809;
  width: 100%;
  margin-top: 50px;
  @media screen and (min-width: 1024px) {
    transform: rotate(90deg);
    position: absolute;
    left: 0;
    right: 0;
    top: 15%;
    bottom: 0;
    height: 0;
    width: 550px;
    margin: auto;
  }
}
.video_box {
  margin: 50px auto 0 auto;
  min-width: 335px;
  min-height: 188px;
  background-color: aquamarine;
  @media screen and (min-width: 667px) {
    width: 560px;
    height: 315px;
  }
  @media screen and (min-width: 834px) {
    width: 720px;
    height: 405px;
  }
  @media screen and (min-width: 1024px) {
    width: 389px;
    height: 219px;
  }
}
